<template>
  <div class="all-container info-container" style="height:calc(100%);margin-top:0em;padding:0; width: 100%;">
    <div class="info-div" >
      <div class="info-header info-header-operate" style="right:0;top:1.2%">
        <combine :option="combineOption" @getSelects="searchLastPay" style="float:right;"></combine>
      </div>
      <div class="list-content detail-content" style="height:calc(100% - 3.7em);margin-top:2.5em;padding: 0.5em 1.3em!important">
        <div class="list-header" style="padding-bottom:0.5px ">
          <ul class="simpleTab">
            <li v-for="(item, $index) in tabs">
              <router-link :to="{path: item.url}">
                {{item.name}}
              </router-link>
            </li>
          </ul>
        </div>
        <div class="tabContent">
          <router-view></router-view>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import combine from  '../../component/childComponent/CombineSelect.vue'
  export default{
    data() {
      return {
        searchData: '',
        tabs: [
          {name: '基础档案', url: '/Main/TotalSearch'},
          {name: '抄表数据', url: '/Main/TotalSearch/MeasureList'},
          {name: '充值记录', url: '/Main/TotalSearch/PayList'},
          {name: '关断记录', url: '/Main/TotalSearch/OnOff'},
          {name: '异常记录', url: '/Main/TotalSearch/Alarm'},
          {name: '换表记录', url: '/Main/TotalSearch/ChangeMeterList'}
        ],
        combineOption: {
          position: 'right',
          inputOpt: {
            width: '100%',
            type: 'typeSearch',
            selectOpt: {
              width: '7em',
              cs: 'client',
              data: [
                {text: '联系方式', value: 'customerContact'},
                {text: '用户地址', value: 'customerAddr'},
                {text: '用户姓名', value: 'customerName'},
                 {text: '门牌编号', value: 'propertyName'}
                //{text: '表计户号', value: 'meterUserNo'},
               // {text: '用户表号', value: 'deviceNo'}
              ],
              text: 'text',
              value: 'value',
              key: 'searchType'
            }
          },
          url: 'pay/fuzzyQuery',
          method: 'post',
        }
      }
    },
    components: {
      combine
    },
    computed: {
        inputOpt: function () {
          return {
            type: 'typeSearch',
            key: 'searchData',
            width: '30em',
            selectOpt: {
              width: '7em',
              cs: 'client',
              data: [
                {text: '用户姓名', value: 'customerName'},
                {text: '用户地址', value: 'customerAddr'},
                {text: '门牌编号', value: 'propertyName'},
                {text: '联系电话', value: 'customerContact'},
                {text: '表计户号', value: 'meterUserNo'},
                {text: '用户表号', value: 'deviceNo'}
              ],
              text: 'text',
              value: 'value',
              key: 'searchType'
            }
          }
        }
    },
    methods: {
      searchLastPay: function (data) {
        if(typeof data.value !== 'undefined') {
          this.$store.state.customerNo = data.value.split(',')[0];
        }
      },
    },
    mounted: function () {
      this.$store.state.customerNo = '';
    }
  }
</script>

<style lang="scss">
.simpleTab{
  font-size: 1.05em;
  li{
    margin: 0 0.2em;
    float: left;
    cursor: pointer;
    color: white;
    background:rgba(#305ea0, 1);
    a{
      color:inherit;
      display: block;
      height:100%;
      padding: 0.7em 2.3em 0.5em 2.3em;
      border-bottom: 0.2em solid transparent;
    }
  }
  li:first-child{
    margin-left:0
  }
  li a:hover{
    border-bottom: 0.2em solid #227fdf;
    background: rgba(255,255,255, 0.1);
  }
  .router-link-exact-active,li a.router-link-exact-active:hover{
    background: #227fdf;
    border-color:transparent;
  }

}
  .simpleTab:after{
    clear:both;
    content:'';
    display:block;
    width:1px;
    height:0;
  }
  .tabContent{
    width:100%;
    height: calc(100% - 3em)
  }
</style>



